.Collection-preview-view {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-template-areas:
            "previewNav"
            "previewDetail";
    padding-bottom: 10px;
    -ms-flex-pack: center;
        justify-content: center;
}

.Collection-preview-view-nav {
    grid-area: previewNav;
    display: grid;
    -ms-flex-pack: center;
        justify-content: center;
    grid-template-columns: 10% 20% 40% 20% 10%;
    grid-template-rows: auto;
    grid-template-areas:
            ". previewPrevious previewCurrent previewNext. ";
    padding-bottom: 10px;
}

.Collection-preview-previous {
    grid-area: previewPrevious;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: center;
        align-self: center;
}
.Collection-preview-current {
    grid-area: previewCurrent;
    -ms-flex-pack: center;
        justify-content: center;
}
.Collection-preview-next {
    grid-area: previewNext;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: center;
        align-self: center;
}

.Collection-preview-detail {
    grid-area: previewDetail;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
}


